@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-light.woff2") format("woff2"), url("../fonts/bloggersans-light.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 300;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-lightitalic.woff2") format("woff2"), url("../fonts/bloggersans-lightitalic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans.woff2") format("woff2"), url("../fonts/bloggersans.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 400;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-italic.woff2") format("woff2"), url("../fonts/bloggersans-italic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-medium.woff2") format("woff2"), url("../fonts/bloggersans-medium.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 500;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-mediumitalic.woff2") format("woff2"), url("../fonts/bloggersans-mediumitalic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-bold.woff2") format("woff2"), url("../fonts/bloggersans-bold.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 700;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-bolditalic.woff2") format("woff2"), url("../fonts/bloggersans-bolditalic.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  color: #ffffff;
  display: flex;
  margin: 0;
  flex-direction: column;
  min-width: 320px;
  background-color: #161c35;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  body {
    font-size: 28px;
    line-height: 24px;
    background-color: #182044;
  }
}

.page-form {
  color: #444444;
}
@media (min-width: 768px) {
  .page-form {
    background-color: #edeff6;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  min-width: 320px;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .container {
    margin: 0 auto;
    padding: 0 5px;
    max-width: 1330px;
  }
}

.section-description {
  padding: 0 25px;
}
@media (min-width: 768px) {
  .section-description {
    padding-left: 34px;
  }
}
@media (min-width: 1440px) {
  .section-description {
    padding-left: 70px;
    padding-right: 0;
  }
}

.section-description__text {
  margin: 0;
  margin-bottom: 18px;
  font-weight: 300;
}
@media (min-width: 768px) {
  .section-description__text {
    line-height: 33px;
    margin-bottom: 31px;
  }
}
@media (min-width: 1440px) {
  .section-description__text {
    margin-bottom: 40px;
  }
}

.section-description__text--bold {
  margin-bottom: 0;
  font-weight: 700;
}
@media (min-width: 768px) {
  .section-description__text--bold {
    font-size: 21px;
    line-height: 25px;
    margin-bottom: 54px;
  }
}
@media (min-width: 1440px) {
  .section-description__text--bold {
    max-width: 470px;
    margin-bottom: 86px;
  }
}

.text-wrapper {
  padding: 0 25px;
}
@media (min-width: 768px) {
  .text-wrapper {
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .text-wrapper {
    margin: 0 auto;
    padding: 0;
    max-width: 1330px;
  }
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.link-button {
  display: block;
  padding: 9px 40px;
  font-size: 18px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: #161c35;
  background-color: #ffd74b;
  border-radius: 25px;
}
@media (min-width: 768px) {
  .link-button {
    font-size: 24px;
    line-height: 24px;
    padding-left: 32px;
  }
}

.link-button--showall {
  position: relative;
  max-width: 270px;
  margin: 0 auto;
  padding: 20px 36px 20px 23px;
  text-align: left;
  border-radius: 10px;
  text-transform: none;
  color: #1d2e5b;
}
.link-button--showall::after {
  position: absolute;
  top: calc(50% - 7px);
  right: 21px;
  width: 13px;
  height: 13px;
  background: url("../img/arrow-right.svg") no-repeat center;
  content: "";
}
@media (min-width: 768px) {
  .link-button--showall {
    max-width: none;
    margin: 0 35px;
    padding: 29px 36px 29px 32px;
    border-radius: 20px;
  }
  .link-button--showall::after {
    right: 33px;
  }
}
@media (min-width: 1440px) {
  .link-button--showall {
    max-width: 275px;
  }
}

.link-button--header {
  margin-bottom: 51px;
  padding-top: 14px;
  padding-bottom: 14px;
  font-size: 17px;
}
@media (min-width: 768px) {
  .link-button--header {
    display: none;
  }
}

.link-button--auth {
  font-size: 17px;
}
@media (min-width: 1440px) {
  .link-button--auth {
    padding: 16px 28px 14px 29px;
    font-size: 20px;
    line-height: 20px;
  }
}

@media (min-width: 768px) {
  .link-button--tablet-show {
    padding: 12px 28px 14px 29px;
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .link-button--tablet-show {
    display: none;
  }
}

.logo__link {
  position: relative;
}
@media (min-width: 768px) {
  .logo__link {
    display: block;
    width: 166px;
    height: 42px;
  }
}
@media (min-width: 1440px) {
  .logo__link {
    display: block;
    width: 200px;
    height: 50px;
  }
}

@media (min-width: 768px) {
  .logo__icon {
    position: absolute;
    z-index: 1;
    top: 12px;
    right: 0;
    width: 107px;
    height: 17px;
  }
}
@media (min-width: 1440px) {
  .logo__icon {
    top: 14px;
    right: -7px;
    width: 129px;
    height: 20px;
  }
}

@media (max-width: 767px) {
  .logo__icon--showtablet {
    display: none;
  }
}
@media (min-width: 1440px) {
  .logo__icon--showtablet {
    width: 200px;
    height: 50px;
  }
}

.user-list {
  display: flex;
  justify-content: center;
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .user-list {
    display: none;
  }
}

.user-list--header {
  display: none;
}
@media (min-width: 1440px) {
  .user-list--header {
    display: flex;
    margin-bottom: 0;
    margin-left: auto;
  }
}
.user-list--header .user-list__link {
  width: 50px;
  height: 50px;
}

.user-list__link {
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
  text-decoration: none;
  color: inherit;
  border-radius: 50%;
}

.user-list__link--tel {
  background-color: #ff8d30;
  background-image: url("../img/icon-call.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.user-list__link--email {
  background-color: #ffbd30;
  background-image: url("../img/icon-top-menu-mail.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.links-list {
  padding-top: 51px;
  padding-bottom: 54px;
}
@media (min-width: 768px) {
  .links-list {
    text-align: left;
    padding: 73px 0 135px 57px;
  }
}
@media (min-width: 1440px) {
  .links-list {
    position: relative;
    display: flex;
    padding: 0;
    padding-left: 90px;
  }
  .links-list::before {
    position: absolute;
    top: calc(50% + 1px);
    left: 1px;
    width: 60px;
    height: 2px;
    background-color: #ffffff;
    content: "";
  }
}

.links-list__link {
  font-size: 30px;
  line-height: 35px;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  color: #192144;
}
@media (min-width: 768px) {
  .links-list__link {
    font-size: 60px;
    line-height: 71px;
  }
}
@media (min-width: 1440px) {
  .links-list__link {
    padding: 21px 16.5px 12px 16.5px;
    font-size: 20px;
    line-height: 20px;
    color: #ffffff;
  }
}

.top-menu__container {
  background-color: #ffffff;
  color: #ffffff;
}

.top-menu--scroll {
  position: fixed;
  z-index: 20;
}
.top-menu--scroll .top-menu__container {
  background-color: #ffffff;
}
.top-menu--scroll .main-nav__toggle::after {
  top: 8px;
  transform: rotate(45deg);
  background-color: #cccccc;
}
.top-menu--scroll .main-nav__toggle::before {
  top: 8px;
  transform: rotate(-45deg);
  background-color: #cccccc;
}

.top-menu--open {
  background-color: #ffffff;
}
.top-menu--open .logo__text {
  fill: #000000;
}
.top-menu--open .main-nav {
  padding-top: 25px;
  padding-bottom: 17px;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
@media (min-width: 768px) {
  .top-menu--open .main-nav {
    padding-top: 40px;
    padding-bottom: 24px;
  }
}
.top-menu--open .main-nav__wrapper {
  top: 64px;
  display: flex;
  position: absolute;
}
@media (min-width: 768px) {
  .top-menu--open .main-nav__wrapper {
    top: 107px;
  }
}
@media (min-width: 1440px) {
  .top-menu--open .main-nav__wrapper {
    position: static;
    padding: 0;
    width: auto;
    background-color: transparent;
  }
}
.top-menu--open .logo__icon path {
  fill: #192144;
}
@media (min-width: 1440px) {
  .top-menu--open .logo__icon path {
    fill: #ffffff;
  }
}
.top-menu--open .main-nav__toggle {
  display: block;
}
.top-menu--open .main-nav__toggle::after {
  top: 2px;
  transform: rotate(45deg);
  background-color: #cccccc;
}
.top-menu--open .main-nav__toggle::before {
  top: 2px;
  transform: rotate(-45deg);
  background-color: #cccccc;
}
@media (min-width: 1440px) {
  .top-menu--open .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .top-menu--open .top-menu__container {
    background-color: #182044;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .top-menu--open {
    background-color: #161c35;
  }
}

@media (min-width: 1440px) {
  .top-menu--close {
    background-color: #161c35;
  }
}
.top-menu--close .top-menu__container {
  background-color: #161c35;
}
@media (min-width: 768px) {
  .top-menu--close .top-menu__container {
    background-color: #182044;
  }
}
@media (min-width: 1440px) {
  .top-menu--close .top-menu__container {
    padding: 0;
  }
}
.top-menu--close .main-nav {
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding-top: 18px;
  padding-bottom: 24px;
}
@media (min-width: 768px) {
  .top-menu--close .main-nav {
    padding-top: 40px;
  }
}
@media (min-width: 1440px) {
  .top-menu--close .main-nav {
    justify-content: flex-start;
    padding-top: 48px;
    padding-left: 5px;
    padding-right: 5px;
    background-color: #161c35;
  }
}
.top-menu--close .main-nav__wrapper {
  top: -805px;
  display: flex;
  position: absolute;
}
@media (min-width: 1440px) {
  .top-menu--close .main-nav__wrapper {
    position: static;
    padding: 0;
    width: auto;
    background-color: transparent;
  }
}
@media (min-width: 1440px) {
  .top-menu--close .main-nav_container {
    margin: 0;
    padding: 0;
  }
}
.top-menu--close .main-nav__toggle {
  display: block;
}
.top-menu--close .main-nav__toggle::after {
  display: block;
  top: 0;
  left: -1px;
  width: 25px;
  height: 3px;
  content: "";
}
.top-menu--close .main-nav__toggle::before {
  display: block;
  top: 8px;
  left: -1px;
  width: 25px;
  height: 3px;
  content: "";
}
@media (min-width: 1440px) {
  .top-menu--close .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .logo--visible-tablet {
    display: none;
  }
}

@media (max-width: 767px) {
  .link-button--tablet-show {
    display: none;
  }
}

.social-list {
  display: flex;
  justify-content: center;
  min-width: 135px;
}
@media (min-width: 768px) {
  .social-list {
    padding-left: 59px;
    justify-content: start;
  }
}
@media (min-width: 1440px) {
  .social-list {
    display: none;
  }
}

.social-list__item:not(:last-child) {
  margin-right: 15px;
}
@media (min-width: 768px) {
  .social-list__item:not(:last-child) {
    margin-right: 0;
  }
}

.social-list__link {
  display: block;
  width: 23px;
  height: 21px;
}
.social-list__link--fb {
  background: url("../img/icon_facebook.svg") 50% 50% no-repeat;
}
.social-list__link--vk {
  background: url("../img/icon_vkontakte.svg") 50% 50% no-repeat;
}
.social-list__link--ig {
  background: url("../img/icon_instagram.svg") 50% 50% no-repeat;
}
.social-list__link--footer {
  width: 45px;
  height: 45px;
  margin-bottom: 37px;
  background-color: #ffd74b;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .social-list__link {
    width: 60px;
    height: 62px;
  }
  .social-list__link--footer {
    width: 62px;
    height: 62px;
    margin-bottom: 0;
  }
  .social-list__link--fb {
    background-size: 14px 26px;
  }
  .social-list__link--vk {
    background-size: 27px 18px;
  }
  .social-list__link--ig {
    background-size: 21px 21px;
  }
}

.contacts-list {
  margin-bottom: 36px;
}
@media (min-width: 768px) {
  .contacts-list {
    padding-left: 40px;
    margin-bottom: 111px;
  }
}
@media (min-width: 1440px) {
  .contacts-list {
    padding-left: 0;
  }
}
@media (min-width: 1440px) {
  .contacts-list--header {
    display: none;
  }
}

.contacts-list__link {
  position: relative;
  display: block;
  padding-top: 12px;
  padding-bottom: 11px;
  padding-left: 61px;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffd74b;
}
.contacts-list__link--header {
  text-align: left;
  color: #192144;
}
.contacts-list__link::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 45px;
  height: 45px;
  background-color: #121936;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  border-radius: 50%;
  content: "";
}
.contacts-list__link--tel::after {
  background-image: url("../img/icon-telephone.svg");
  background-position: 50% 50%;
}
.contacts-list__link--mail::after {
  background-image: url("../img/icon-mail.svg");
  background-position: 50% 50%;
}
@media (min-width: 768px) {
  .contacts-list__link {
    font-size: 28px;
    line-height: 28px;
    padding: 20px 0 20px 86px;
  }
  .contacts-list__link::after {
    width: 68px;
    height: 68px;
    top: -2px;
    left: -5px;
  }
  .contacts-list__link--tel::after {
    background-size: 27px 26px;
  }
  .contacts-list__link--mail::after {
    background-size: 28px 19px;
  }
}
@media (min-width: 1440px) {
  .contacts-list__link {
    padding: 31px 0 31px 121px;
    padding-left: 121px;
  }
  .contacts-list__link::after {
    top: -1px;
    left: 0;
    width: 90px;
    height: 90px;
  }
  .contacts-list__link--tel::after {
    background-size: 38px 34px;
  }
  .contacts-list__link--mail::after {
    background-size: 37px 25px;
  }
