@media (min-width: 1440px) {
  .contacts-list__item:not(:last-child) {
    margin-right: 110px;
  }
}

.main-nav {
  display: flex;
  padding: 25px 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-nav {
    padding-top: 40px;
    padding-bottom: 27px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    justify-content: flex-start;
    padding-top: 48px;
    padding-left: 5px;
    padding-right: 5px;
    background-color: #161c35;
  }
}

.main-nav__wrapper {
  position: static;
  left: 0;
  top: -400px;
  display: flex;
  padding-bottom: 37px;
  text-align: center;
  flex-direction: column;
  width: 100%;
  background-color: #ffffff;
  z-index: 5;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    padding-bottom: 73px;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    position: static;
    padding: 0;
    width: auto;
    background-color: transparent;
  }
}

.main-nav__toggle {
  display: none;
  position: relative;
  width: 25px;
  height: 11px;
  border: none;
  background-color: transparent;
}
.main-nav__toggle::after {
  position: absolute;
  top: 6px;
  left: 0;
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 2px;
  content: "";
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.main-nav__toggle::before {
  position: absolute;
  top: 14px;
  left: 0;
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 2px;
  content: "";
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    margin-left: 24px;
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__item {
  display: flex;
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .main-nav__item {
    width: 60px;
    height: 62px;
  }
}

@media (min-width: 1440px) {
  .main-nav__logo {
    margin-left: -6px;
    margin-right: 45px;
  }
}

@media (min-width: 1440px) {
  .main-nav_container {
    margin: 0;
    padding: 0;
  }
}

.promo {
  padding-top: 27px;
  padding-bottom: 39px;
  background-color: #161c35;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
@media (min-width: 768px) {
  .promo {
    padding-top: 44px;
    background-color: #182044;
  }
}
@media (min-width: 1440px) {
  .promo {
    padding-top: 83px;
    padding-bottom: 124px;
    background-image: url("../img/promo-bg-desktop.png");
    background-position-y: -122px;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.promo__container {
  padding: 0;
  background-image: url("../img/promo-bg.jpg");
  background-repeat: no-repeat;
  background-position-y: 138px;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
@media (min-width: 768px) {
  .promo__container {
    background-image: url("../img/promo-bg-tablet.png");
    background-position-y: 223px;
  }
}
@media (min-width: 1440px) {
  .promo__container {
    background-image: none;
  }
}

.promo__heading {
