.catalog-card__fieldset {
  position: relative;
  margin: 0;
  padding: 0;
  border: none;
}
.catalog-card__fieldset::after {
  position: absolute;
  top: -26px;
  left: -25px;
  width: 270px;
  height: 1px;
  background-color: #edeff6;
  content: "";
