}
.catalog-card__item--belgium::after {
  background-image: url("../img/belgium.svg");
}
.catalog-card__item--chech::after {
  background-image: url("../img/chech.svg");
}
.catalog-card__item--usa::after {
  background-image: url("../img/usa.svg");
}
.catalog-card__item--australia::after {
  background-image: url("../img/australia.svg");
}
.catalog-card__item--dominican::after {
  background-image: url("../img/dominica.svg");
}
.catalog-card__item--uk::after {
  background-image: url("../img/uk.svg");
}
.catalog-card__item--germany::after {
