@media (min-width: 768px) {
  .alphabet {
    display: none;
    padding: 35px 73px 33px 27px;
    border-right: 1px solid #d4d9eb;
    background-color: #ffffff;
    box-shadow: 0 8px 16px rgba(54, 80, 148, 0.18);
    border-bottom-left-radius: 5px;
  }
}

.alphabet__list {
  display: none;
  grid-template-columns: repeat(4, 44px) 43px;
  grid-template-rows: repeat(5, 41px);
  background-color: #ffffff;
  border-left: 1px solid #cbced9;
}
.alphabet__list--active {
  display: grid;
}
@media (min-width: 768px) {
  .alphabet__list {
    border-top: 1px solid #cbced9;
    grid-template-columns: repeat(4, 53px) 54px;
    grid-template-rows: repeat(5, 70px);
  }
}

.alphabet__sublist {
  display: none;
  position: absolute;
  background-color: #ffffff;
  width: 100%;
  top: 205px;
  left: 0;
  padding: 20px 20px 15px 19px;
  border: 1px solid #cbced9;
  border-top: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #1d2e5b;
  border-radius: 0 0 5px 5px;
}
@media (min-width: 768px) {
  .alphabet__sublist {
    top: 0;
    left: 100%;
    width: 210px;
    min-height: 100%;
    padding-top: 35px;
    font-size: 20px;
    line-height: 20px;
    border: none;
    border-radius: 0;
    box-shadow: 5px 8px 16px rgba(54, 80, 148, 0.18);
    z-index: 3;
  }
}

.alphabet__button {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  border: none;
  background-color: #ffffff;
  color: #1d2e5b;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
}
.alphabet__button--active {
  background-color: #edeff6;
}
.alphabet__button--active + .alphabet__sublist {
  display: block;
}
@media (min-width: 768px) {
  .alphabet__button {
    font-size: 20px;
    line-height: 20px;
  }
  .alphabet__button--active {
    background-color: #1d2e5b;
    color: #ffffff;
  }
}

.alphabet__item {
  border-right: 1px solid #cbced9;
  border-bottom: 1px solid #cbced9;
}

.alphabet__list--letters {
  max-width: 220px;
  border-top: 1px solid #cbced9;
}
.alphabet__list--letters .alphabet__button--active {
  background-color: #1d2e5b;
  color: #ffffff;
}
.alphabet__list--letters .alphabet__button--active + .alphabet__sublist {
  top: 599px;
  padding-right: 50px;
  padding-left: 50px;
  background-color: #e2e5f2;
  z-index: 1;
}

.alphabet__subitem:not(:last-child) {
  margin-bottom: 4px;
}
@media (min-width: 768px) {
  .alphabet__subitem:not(:last-child) {
    margin-bottom: 10px;
  }
}

@media (max-width: 1329px) {
  .countries-list__letter {
    display: none;
  }
}

.countries-list {
  display: block;
  background-color: transparent;
  border: none;
  font-size: 16px;
  font-weight: 400;
  color: #444444;
}

.countries-list__sublist {
  display: none;
  padding-top: 18px;
  padding-bottom: 28px;
}
.countries-list__sublist--active {
